.favorite-list {
  display: flex;

  flex-wrap: wrap;
}
.news__item-fav {
  @media screen and (min-width: 1280px) {
    width: 393px;
    margin-right: 30px;
    display: inline-flex;
    flex-direction: column;

    &:nth-child(3n) {
      margin-right: 0;
    }

    &:nth-last-child(-n + 3) {
      margin-bottom: 0;
    }
  }

  margin-bottom: 30px;

  margin-right: auto;

  @media screen and (max-width: 767px) {
    height: 630px;
  }
  @media screen and (min-width: 768px) and(max-width:1279px) {
    width: 351px;
    height: 675px;
    display: inline-flex;
    flex-direction: column;
    margin-right: 30px;
    &:nth-child(2n) {
      margin-right: 0;
    }
    &:nth-last-child(-n + 2) {
      margin-bottom: 0;
    }
  }
}
